@import url(https://fonts.googleapis.com/css2?family=Space+Mono&display=swap);
body {
  margin: 0;
  font-family: 'Space Mono', monospace;
  font-size: 70%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  zoom: 1.5;
  overflow: hidden;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #141d2f;
  height: 100vh;
  grid-row-gap: 20px;
  row-gap: 20px;
  padding-top: 125px;
}

.header {
  display: flex;
  justify-content: space-between;
  width: 550px;
}

h4 {
  display: flex;
}
.header {
  align-items: flex-end;
}

.title {
  font-size: 150%;
}

h3 {
  margin-bottom: 0px;
}
img {
  height: 100px;
  width: 100px;
  border-radius: 50%;
  object-fit: cover;
}

.profile-container {
  width: 550px;
  display: flex;
  align-items: stretch;
  background-color: #1e2a47;
  border-radius: 10px;
  padding-top: 20px;
}

.profile-picture {
  display: flex;
  justify-content: center;
  flex: 1 1;
}

.profile-info {
  flex: 2 1;
}

.name-date {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.name-date > p {
  display: flex;
  align-items: center;
}

p.name {
  display: flex;
  font-size: 175%;
  margin-bottom: 0px;
}

p.date {
  display: flex;
  margin-bottom: 0px;
  margin-right: 30px;

}

a.username {
  margin-top: 0px;
  margin-bottom: 20px;
  color: #0079ff;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.bio > p {
  margin-top: 10px;
  margin-bottom: 30px;
}

.user-stats {
  display: flex;
  align-items: center;
  justify-content: space-around;
  border-radius: 10px;
  background-color: #141d2f;
  margin-right: 30px;
  height: 75px;
}

p.repo-data,
p.followers-data,
p.following-data {
  font-weight: bold;
  font-size: 120%;
}

.repo {
  display: flex;
  flex-direction: column;
}

.followers {
  display: flex;
  flex-direction: column;
}

.following {
  display: flex;
  flex-direction: column;
}



.user-links {
  display: flex;
  width: 300px;
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
  margin-top: 30px;
  margin-bottom: 30px;
}

.location-blog {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  grid-row-gap: 15px;
  row-gap: 15px;
}

p {
  margin-top: 0px;
  margin-bottom: 0px;
}

.blog-name {
  color: white;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.twitter-company {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  grid-row-gap: 15px;
  row-gap: 15px;
}

.twitter-name {
  color: white;
  text-decoration: none;
}

.company-name {
  color:white;
}

.twitter,
.location,
.blog,
.company {
  display: flex;
  align-items: center;
}

.twitter > svg,
.location > svg,
.blog > svg,
.company > svg {
  padding-right: 10px;
}

.searchbar-container {
  display: flex;
  align-items: center;
  width: 550px;
  height: 40px;
  background-color: #1e2a47;
  border-radius: 10px;
}

.searchbar-container > svg {
  margin-left: 20px;
  margin-right: 15px;
}

form {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

input {
  font-family: "Space Mono", monospace;
  color: white;
}

::-webkit-input-placeholder {
  color: rgb(180, 180, 180);
}

:-ms-input-placeholder {
  color: rgb(180, 180, 180);
}

::placeholder {
  color: rgb(180, 180, 180);
}

.search-input {
  display: flex;
  width: 400px;
  background-color: transparent;
  border: none;
  outline: none;
}

.search-btn {
  font-family: "Space Mono", monospace;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 20px;
  border-radius: 5px;
  border-style: none;
  background-color: #0079ff;
  height: 30px;
  width: 65px;
  font-size: 100%;
  transition: all 0.5s;
  cursor: pointer;
}

.search-btn span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
  color: white;
}

.search-btn span:after {
  content: "\00bb";
  position: absolute;
  opacity: 0;
  top: 0;
  right: -8px;
  transition: 0.5s;
}

.search-btn:hover span {
  padding-right: 15px;
}

.search-btn:hover span:after {
  opacity: 1;
  right: 0;
}

